@import url(https://fonts.googleapis.com/css?family=Work+Sans);
@import url(https://fonts.googleapis.com/css?family=Roboto:100);

html {
  height: 100vh;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: 22px;
  background: rgb(255, 255, 255);
  color: rgba(0, 0, 0);
  min-height: 768px;
  min-width: 360px;
  min-height: 100vh;
  width: 100%;
}

.App.dark {
  background: #1b1b1d;
  color: rgb(255, 255, 255);
}

h1 {
  margin: 0;
  padding: 25px;
  color: white;
  font-family: 'Work Sans', sans-serif;
  text-shadow:
    1px 1px 0px #000,
    -1px -1px 1px #000,  
    1px -1px 1px #000,
    -1px 1px 1px #000,
    1px 1px 1px #000;
  -webkit-text-shadow:
    1px 1px 0px #000,
    -1px -1px 1px #000,  
    1px -1px 1px #000,
    -1px 1px 1px #000,
    1px 1px 1px #000;
  -moz-text-shadow:
    1px 1px 0px #000,
    -1px -1px 1px #000,  
    1px -1px 1px #000,
    -1px 1px 1px #000,
    1px 1px 1px #000;
}

a {
  display: block;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  color: rgb(112, 167, 184);
}

a:hover {
  color: rgb(192, 130, 50);
}

.github {
  display: block;
  font-size: 58px;
}

.app-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, 200px);
  grid-gap: 1em;
  justify-content: center;
}

.comp {
  padding: 5px;
  height: 200px;
  position: relative;
  color: inherit;
  border-color: inherit;
  min-height: 200px;
  border-collapse: separate;
  box-shadow:
  0 0 4px 1px rgba(0, 0, 0, .03), 
  0 3px 24px rgba(48, 51, 49, 0.2);
  -webkit-box-shadow:
  0 0 4px 1px rgba(0, 0, 0, .03), 
  0 3px 24px rgba(48, 51, 49, 0.2);
  -moz-box-shadow:
  0 0 4px 1px rgba(0, 0, 0, .03), 
  0 3px 24px rgba(48, 51, 49, 0.2);
}

.comp.info a {
  padding-top: 20%;
}

.App.dark .comp {
  box-shadow:
  0 0 4px 1px rgba(0, 0, 0, .02), 
  0 3px 24px rgba(0, 0, 0, 0.7);
  -webkit-box-shadow:
  0 0 4px 1px rgba(0, 0,0,.02), 
  0 3px 24px rgba(0, 0, 0, 0.7);
  -moz-box-shadow:
  0 0 4px 1px rgba(0, 0, 0, .02), 
  0 3px 24px rgba(0, 0, 0, 0.7);
}

.rainbow0 {
  color: rgb(255, 32, 107);
}

.rainbow1 {
  color: rgb(250, 96, 96);
}

.rainbow2 {
  color: rgb(204, 121, 95);
}

.rainbow3 {
  color: rgb(158, 160, 18);
}

.rainbow4 {
  color: rgb(33, 151, 59);
}

.rainbow5 {
  color: rgba(29, 167, 156);
}

.rainbow6 {
  color: rgb(106, 134, 158);
}

.rainbow7 {
  color: rgb(136, 123, 197);
}

.rainbow8 {
  color: rgb(147, 111, 155);
}

.rainbow9 {
  color: rgb(167, 120, 134);
}

h3, h5 {
  line-height: 0;
  margin: 0.6em;
}

footer {
  width: 100%;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  margin: 25px 0 15px 0;
}

footer a {
  text-decoration: none;
  color: inherit;
}

footer a:hover {
  color: rgb(204, 154, 97);
}




.floating-button-wrapper {
  position: absolute;
  left: calc(50% - 50px);
  top: calc(50% - 20px);
}

.btn {
  display: block;
  width: 100px;
  height: 50px;
  padding: 15px;
  text-align: center;
  text-decoration: none;
  border-radius: 5px;
  box-sizing: border-box;
  color: inherit;
  background: inherit;
  border-collapse: separate;
  box-shadow:
    3px 3px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -webkit-box-shadow:
    3px 3px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -moz-box-shadow:
    3px 3px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -webkit-transition: box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
  transition: box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
  transition: box-shadow 0.3s ease-out, transform 0.3s ease-out;
  transition: box-shadow 0.3s ease-out, transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.btn:hover {
  color: rgba(255, 126, 126, 0.5);
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px);
  border-collapse: separate;
  box-shadow:
    4px 4px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -webkit-box-shadow:
    4px 4px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -moz-box-shadow:
    4px 4px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
}

.btn:active {
  color: rgb(255, 126, 126);
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  border-collapse: separate;
  box-shadow:
    2px 2px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -webkit-box-shadow:
    2px 2px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -moz-box-shadow:
    2px 2px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
}

@media only screen and (max-width:768px) {
  .btn.active {
    color: rgb(255, 126, 126);
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
    border-collapse: separate;
    box-shadow:
      2px 2px 0px,
      -1px -1px 0,  
      1px -1px 0,
      -1px 1px 0,
      1px 1px 0;
    -webkit-box-shadow:
      2px 2px 0px,
      -1px -1px 0,  
      1px -1px 0,
      -1px 1px 0,
      1px 1px 0;
    -moz-box-shadow:
      2px 2px 0px,
      -1px -1px 0,  
      1px -1px 0,
      -1px 1px 0,
      1px 1px 0;
  }
}
.scorebox-comp {
  box-sizing: border-box;
  position: absolute;
  top: 5px;
  left: 5px;
  line-height: 0;
  z-index: 20000;
}

.scorebox-wrapper {
  display: grid;
  grid-template-columns: -webkit-min-content 1fr;
  grid-template-columns: min-content 1fr;
  grid-gap: 0.1em;
}

.icon .shift {
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}


.score {
  overflow: auto;
  line-height: 1.1em;
}
.switch-wrapper {
  position: absolute;
  top: calc(50% - 20px);
  left: calc(50% - 50px);
}

.switch {
  display: block;
  position: relative;
 	width: 100px;
  height: 50px;
  border-radius: 50px;
  background: rgba(132, 84, 223, 0.5);
  -webkit-tap-highlight-color: transparent;
  border: 1px solid;
  cursor: pointer;
  border-collapse: separate;
  box-shadow:
    inset 0px 0px 2px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -webkit-box-shadow:
    inset 0px 0px 2px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -moz-box-shadow:
    inset 0px 0px 2px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -webkit-transition: background 0.4s ease-out;
  transition: background 0.4s ease-out;
}

.switch.off {
  background: rgba(139, 178, 209, 0.5);
}

#switch {
 	clip: rect(0 0 0 0);
  position: absolute;
  visibility: hidden;
}

.slider {
  height: 40px;
  width: 40px;
  background: #fff;
  border: 1px solid;
  border-radius: 50%;
  position: absolute;
  top: 4px;
  left: 53px;
  border-collapse: separate;
  box-shadow: 0px 0px 5px; 
  -webkit-box-shadow: 0px 0px 5px;
  -moz-box-shadow: 0px 0px 5px;
  -webkit-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
}

.slider.off {
  left: 5px;
} 
.checkbox-comp {
  position: absolute;
  top: calc(50% - 75px);
  left: calc(50% - 25px);
}

.checkbox-wrapper {
  display: grid;
  grid-template-rows: 50px 50px 50px;
  width: 50px;
  border: 1px solid;
  border-radius: 0.5em;
  border-collapse: separate;
  box-shadow:
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -webkit-box-shadow:
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -moz-box-shadow:
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
}

.container {
  display: block;
  position: relative;
  cursor: pointer;
  font-size: 22px;
  height: 100%;
  -webkit-tap-highlight-color: transparent;
  background-color: inherit;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.container input {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}

.checkmark  {
  position: absolute;
  top: calc(50% - 12.5px);
  left: calc(50% - 12.5px);
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%;
  -webkit-transition: background-color 0.3s ease-out;
  transition: background-color 0.3s ease-out;
}

.checkmark.red {
  background-color: rgba(230, 94, 94, 0.25);
}

.checkmark.yellow {
  background-color: rgba(224, 198, 51, 0.25);
}

.checkmark.green {
  background-color: rgba(71, 231, 71, 0.25);
}

.container input:hover ~ .checkmark {
  background-color: rgba(94, 94, 117, 0.5);
}

.container input:checked ~ .checkmark.red {
  background-color: rgb(230, 94, 94);
}

.container input:checked ~ .checkmark.yellow {
  background-color: rgb(224, 198, 51);
}

.container input:checked ~ .checkmark.green {
  background-color: rgb(71, 231, 71);
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.container input:checked ~ .checkmark:after {
  display: block;
}

.container .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
}

.App.dark .half-circle {
  background: #1b1b1d;
}
